import React, { useContext } from 'react';
// import AnchorLink from 'react-anchor-link-smooth-scroll';
import { ThemeContext } from 'providers/ThemeProvider';
import { Container, Button } from 'components/common';
// import art from 'assets/images/du-jin_fu-sheng.jpg';
import { Wrapper, IntroWrapper, Details, Thumbnail } from './styles';
import {StaticImage} from "gatsby-plugin-image";
import Link from "gatsby-link";

export const About = ({ lang }) => {
  const { theme } = useContext(ThemeContext);
  const isEn = lang === "en"
  return (
    <Wrapper id={"about"}>
      <IntroWrapper as={Container}>
        <Details theme={theme}>
        <h1>{ isEn ? "About Me" : "关于" }</h1>
          { isEn
            ? <p>I am a professor in the <a href="http://religion.rutgers.edu/tao-jiang" target="_blank" rel="noreferrer">Department of Religion</a> and an associate member of the graduate faculty in the <a href="https://philosophy.rutgers.edu" target="_blank" rel="noreferrer">Department of Philosophy</a> at Rutgers University, New Brunswick, New Jersey, US.</p>
            : <p>蒋韬，罗格斯大学（新布朗维斯克校区，新泽西州，美国）<a href="http://religion.rutgers.edu/tao-jiang" target="_blank" rel="noreferrer">宗教系</a>教授，<a href="https://philosophy.rutgers.edu" target="_blank" rel="noreferrer">哲学系</a>兼职教授。</p>
          }
          <Button as={Link} to={"/" + (isEn ? "" : "cn/") + "about"}>
            { isEn ? "Read more" : "更多信息" }
          </Button>
        </Details>
        <Thumbnail>
          <StaticImage
              src={"../../../assets/images/about.jpg"}
              alt={"Tao Jiang, Professor of Chinese Philosophy and Buddhist Philosophy at Rutgers University, New Brunswick."}
              placeholder="blurred"
              style={{ height: "100%", width: "100%" }}
              imgStyle={{ objectFit: "contain" }}
          />
          {/*<img src="https://taojiangscholar.com/static/media/about.171905f3.JPG"*/}
          {/*     alt="Tao Jiang, Professor of Chinese Philosophy and Buddhist Philosophy at Rutgers University, New Brunswick."*/}
          {/*/>*/}
        </Thumbnail>
      </IntroWrapper>
    </Wrapper>
  );
};
